    color: white !important;
}

.dev-team .dev-contact-icon:hover
{
    color: orangered !important;
}

.dev-member
{
    padding: 10px 5px;
}

.footer-nav-link
{
    display: table;
    /* background-color: aqua; */
    width: 100%;
    text-align: center;
}

.footer-nav-link ul 
{
 list-style: none;
 padding: 10px;
}

.footer-nav-link ul li
{
    display: inline;
    padding: 20px 5px;
    text-align: center;
    font-family: 'Roboto', sans-serif;
    color: white;
}

.footer-nav-link ul li a
{
    font-family: 'Roboto', sans-serif;
    color: white;
}

.footer-nav-link ul li a:hover
{
    color: orangered;
}

.minds-2021-mail
{
    color: #04d9ff !important;
    text-decoration: none !important;
    text-transform: lowercase !important;
    font-family: 'Roboto', sans-serif !important;
    margin-bottom: 20px;
    font-size: 18px !important;
}

.minds-2021-mail:hover
{
    color: orangered !important;
}
.schedule-minds-logo
{
  position: relative;
  top: 0;
  left: 0;
  padding-top: 10px;
  padding-left: 10px;
  z-index: 1000;
  /* float: left; */
}

.schedule-minds-logo img
{
  height: auto;
  width: 200px;
}
.schedule-title
{
    margin: 20px 10px;
    font-size: 35px;
}

.schedule-title h4
{
    /* margin: 20px 10px; */
    font-size: 35px !important;
    color: rgb(255, 255, 255) !important;
    font-family: 'Mohave', sans-serif !important;
    font-weight: 700 !important;
    text-transform: capitalize;                                                
}


@media screen and (max-width: 992px) 
{
    #tableContainer
    {
        width: 100% !important;
    }
}
*
{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    text-decoration: none;    
    scroll-behavior: smooth !important;
}

body
{
    background: #000046;  
    background-image: radial-gradient(circle, #1cb5e0, #0087c0, #005a9c, #002f73, #000046);
    scroll-behavior: smooth !important;
}
